.graphics header {
	font-family: 'Raleway', sans-serif;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 50;
	color: white;
	// background-color: rgba(0, 0, 0 ,0.1)
	padding: 10px 0;

	.navbar {
		background-color: transparent;
		.navbar-brand {
			a:hover {
				color: white; }
			svg {
				@include transform (rotate(90deg));
				@include transition (all, 0.5s, ease);
				height: 60px;
				margin-left: 12px; }
			path {
				fill: white;
				stroke: white; }
			.navbar-brand-txt {
				margin-left: 2rem;
				span.logo-name {
					letter-spacing: 4px;
					font-weight: 500;
					font-size: 16px; }
				span.logo-job {
					font-weight: 400;
					letter-spacing: 1.9px;
					color: rgba(255, 255, 255, 0.6);
					margin-top: 2px; } } }
		.navbar-item,
		.navbar-link {
			padding: 0px;
			line-height: 1;
			color: white; }
		.navbar-link.is-active,
		.navbar-link:hover,
		a.navbar-item.is-active,
		a.navbar-item:hover {
			background-color: transparent;
			color: #FF5252;
			text-decoration: none; }
		.navbar-burger {
			height: auto;
			color: white;
			span {
				height: 2px;
				width: 20px; }
			&:hover {
				background-color: transparent; } }
		.navbar-menu {
			font-weight: 400;
			text-transform: uppercase;
			ul {
				display: flex;
				flex-direction: row;
				align-items: center; }
			li {
				color: white;
				margin-right: 20px;
				float: left;
				font-weight: 400;
				&:hover {
					color: rgba(255, 255, 255, 0.5); }
				&:active {
					color: white; }
				&:last-child {
					margin-right: 0px; } }
			.current-page {
				border-bottom: 1px solid; } } } }
