.graphics.about {
	background: linear-gradient(to bottom, #C638EF 0%, #17BAFF 60%, #657CF8 100%);

	main {
		position: relative;
		z-index: 20;
		.columns .column:nth-child(2) {
			display: flex;
			justify-content: center;
			.card {
				width: 400px;
				background-color: white;
				color: black;
				border-radius: 10px;
				padding: 40px 20px;
				text-align: left;
				box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1), 0px 0px 15px rgba(0, 0, 0, 0.2), 0px 0px 5px rgba(0, 0, 0, 0.1);
				.top {
					text-align: center;
					& > img {
						border-radius: 100%;
						height: 100px;
						width: 100px;
						margin-bottom: 20px; }
					h2 {
						font-size: 24px;
						font-weight: normal;
						& ~ div {
							display: flex;
							justify-content: center;
							margin-top: 10px;
							margin-bottom: 20px;
							p {
								margin: 0 5px;
								font-style: italic;
								width: fit-content; }
							img {
								height: 19px; } } } }
				.middle, .bottom {
					border-top: 1px solid rgba(0, 0, 0, 0.2);
					padding-top: 20px;
					h3 {
						font-size: 16px;
						font-weight: bold;
						margin-bottom: 20px; } }
				.middle {
					padding-bottom: 15px;
					p {
						margin: 0 10px 5px 0;
						opacity: 0.3;
						font-size: 16px;
						color: white;
						background-color: black;
						border-radius: 3px;
						padding: 5px;
						width: fit-content;
						line-height: 1.2; } }
				.bottom {
					div {
						display: flex;
						flex-direction: row;
						a {
							display: flex;
							height: 30px;
							width: 30px;
							border-radius: 100%;
							justify-content: center;
							align-items: center;
							margin-right: 10px;
							&:nth-child(1) {
								background-color: #24292E; }
							&:nth-child(2) {
								background-color: #0073B0; }
							&:nth-child(3) {
								background-color: #EB4C88; }
							i {
								color: white;
								font-size: 16px; } } } } } }
		.action {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			margin-top: 200px;
			margin-bottom: 300px;
			h2 {
				font-size: 24px;
				font-weight: 500;
				text-shadow: 0px 2px 25px rgba(0, 0, 0, 0.15), 0px 1px 2px rgba(0, 0, 0, 0.35);
				margin-bottom: 20px; }
			button {
				border: 0;
				border-radius: 6px;
				background-color: white;
				padding: 15px 80px;
				box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 2px 2px rgba(0, 0, 0, 0.25);
				a {
					display: flex;
					img {
						height: 20px; }
					p {
						font-size: 18px;
						margin: 0 5px; } } } } }

	.waves {
		.wave1 {
			bottom: 0; }
		.wave2 {
			z-index: 5;
			bottom: 0; } } }
