.graphics.contact {
	background: linear-gradient(to bottom, #C638EF 0%, #17BAFF 80%);

	main {
		text-align: center;

		h1 {
			margin-bottom: 0; }

		p {
			margin: 50px 0; }

		form {
			display: flex;
			flex-direction: column;
			align-items: center;
			& * {
				height: 40px;
				border: 1px solid transparent;
				border-radius: 7px;
				padding: 0 15px;
				font-size: 20px;
				margin-bottom: 5px;
				width: 60%;
				box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 2px 2px rgba(0, 0, 0, 0.15); }
			input, textarea {
				color: $grey;
				&::placeholder {
					color: $grey;
					opacity: .2; }
				&[type="submit"] {
					width: 30%;
					background-color: #2A3039;
					color:  $white;
					font-size: 1em;
					margin: 15px 0 0 0;
					transition: all .25s ease;
					&:hover {
						cursor: pointer;
						background-color: #1f2124; }
					&:focus {
						border: 1px solid transparent;
						box-shadow: none; } }
				&:focus {
					outline: 0;
					border-color: #A73AD1;
					box-shadow: 0px 4px 4px rgba(167, 58, 209, 0.45), 0px 2px 2px rgba(167, 58, 209, 0.35);
					&::placeholder {
						color: #A73AD1; } } }
			.select {
				padding: 0;
				border: 0;
				select {
					color: $grey;
					height: 100%;
					width: 100%;
					box-shadow: none;
					&:focus {
						border-color: #A73AD1;
						box-shadow: 0px 4px 4px rgba(167, 58, 209, 0.45), 0px 2px 2px rgba(167, 58, 209, 0.35); } }
				&:not(.is-multiple):not(.is-loading)::after {
					border-color: $grey; } }
			textarea {
				padding: 15px;
				min-height: 100px;
				max-height: 300px;
				resize: vertical; } } }

	footer {
		margin-top: 250px; }

	.waves {
		.wave1 {
			bottom: 0;
			z-index: -1; }
		.wave2 {
			bottom: 0; } } }
