.audio.portfolio {

	.moveArrow-afterClose {
		bottom: 20px !important; }
	.moveArrow-afterOpen {
		bottom: 90px !important; }
	.audioPlayer {
		position: fixed;
		bottom: 0;
		width: 100%;
		padding: 10px;
		background-color: white;
		box-shadow: 0 0 50px rgba(0, 0, 0, 0.25);
		z-index: 50;
		transform: translateY(70px);
		@include transition (transform, 0.25s, ease);
		.closePlayer {
			position: absolute;
			right: 10px;
			height: 23px;
			width: 23px;
			background-color: rgba(0, 0, 0, 0);
			display: flex;
			justify-content: center;
			align-items: center;
			&:hover {
				cursor: pointer; }
			i {
				font-family: "FontAwesome", regular;
				font-size: 1.1em;
				line-height: 15px;
				width: 15px;
				z-index: -1;
				@include transition (all, 0.25s, ease); } }
		.container {
			display: flex;
			flex-direction: row;
			align-items: center; }
		.art {
			background-color: #333;
			height: 50px;
			width: 50px;
			margin-right: 25px; }
		.info {
			display: flex;
			flex-direction: row;
			align-items: center;
			p {
				font-weight: 500; }
			span {
				white-space: pre;
				font-weight: normal; }
			.trackCount {
				white-space: pre; } }
		.audio-controls {
			display: flex;
			flex-direction: row-reverse;
			align-items: center;
			width: 100%;
			.progress-bar {
				width: 100%;
				height: 7px;
				border-radius: 10px;
				background-color: #dedede;
				margin: 0 25px;
				cursor: pointer;
				.current-pos {
					height: 100%;
					max-width: 100%;
					min-width: 0%;
					width: 0%;
					@include animation (play, 10s, infinite, linear);
					@include keyframes (play) {
						from {width: 0%};
						to {width: 100%}; }
					animation-play-state: paused;
					background-color: #ff5252;
					border-radius: 10px;
					display: flex;
					align-items: center;
					&::after {
						content: '';
						background-color: #ff5252;
						height: 7px;
						width: 7px;
						position: relative;
						border-radius: 100%;
						left: calc(100% - 7px);
						top: 0%;
						box-shadow: 0 0 1px rgba(0, 0, 0, 0);
						@include transition (all, 0.1s, ease); }
					&:hover::after {
						height: 13px;
						width: 13px;
						left: calc(100% - 13px);
						box-shadow: 0 0 1px rgba(0, 0, 0, 0.5); } } }
			.seek {
				display: flex;
				align-items: center;
				.play {
					border: 2px solid #333;
					border-radius: 100%;
					height: 40px;
					width: 40px;
					margin: 0 30px;
					display: flex;
					align-items: center;
					justify-content: center;
					@include transition (all, 0.15s, ease);
					&:hover {
						cursor: pointer;
						opacity: 0.5; }
					&::after {
						content: '';
						width: 0;
						height: 0;
						border-style: solid;
						border-width: 10px 0 10px 17.3px;
						border-color: transparent transparent transparent #333333;
						transform: translateX(2.5px) scale(0.75); } }
				.previous, .next {
					display: flex;
					background-color: #333;
					height: 15px;
					width: 3px;
					align-items: center;
					@include transition (all, 0.15s, ease);
					&:hover {
						cursor: pointer;
						opacity: 0.5; }
					&:after {
						content: '';
						width: 0;
						height: 0;
						border-style: solid;
						border-width: 10px 0 10px 17.3px;
						border-color: transparent transparent transparent #333333;
						transform: translateX(-2px) scale(0.75) rotate(180deg); } }
				.next {
					transform: rotate(180deg); } } } }
	main {
		h1 {
			margin-bottom: 25px; }
		p {
			text-align: center;
			margin-bottom: 25px;
			font-weight: 400; }
		span {
			cursor: pointer;
			&:focus, &:visited, &:hover, &:active {
				color: $red; } }
		.filter-active {
			color: $red;
			text-decoration: underline; }
		.artwork-preview {
			position: relative;
			&:hover .overlay {
				opacity: 1; } }
		.artwork-preview-box {
			width: 100%; }
		.columnHide {
			display: none;
			padding: 5px; }
		.columnShow {
			display: block;
			padding: 5px; }
		.artwork-preview img {
			display: inline-block;
			margin: 0;
			vertical-align: top;
			width: 100%; }
		.artwork-overlay {
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			height: 100%;
			width: 100%;
			opacity: 0;
			@include transition (all, 0.25s, ease);
			background-color: rgba(0, 0, 0, 0.85); }
		.artwork-overlay-text {
			color: white;
			font-size: 20px;
			position: absolute;
			top: 50%;
			left: 50%;
			@include transform (translate(-50%, -50%)); }
		ul.artwork-overlay {
			list-style: none; }
		.artwork-overlay ul > li {
			margin-bottom: 20px;
			width: 200px;
			font-weight: 300;
			&:first-child {
				font-weight: 700; }
			&:last-child {
				color: rgba(185, 185, 185, 1);
				margin-bottom: 0px; } }
		.artwork-preview:hover .artwork-overlay {
			opacity: 1; }
		li.artwork-band {
			font-weight: 500; }
		li.artwork-album,
		li.artwork-task {
			font-size: 18px; }
		.artwork-overlay iframe {
			border: 0;
			width: 100% !important;
			height: 100% !important;
			position: absolute;
			bottom: 0%; }
		#maintextlink {
			color: white; }
		.theme-dark #infolayer {
			text-align: center; }
		.video-thumbnail {
			background-color: black;
			overflow: hidden;
			img {
				vertical-align: top;
				@include filter (none);
				@include transform (scale(1));
				cursor: pointer;
				@include transition (all, 0.25s, ease);
				&:hover {
					@include filter (opacity(0.8)blur(5px));
					@include transform (scale(1.2)); } } }
		.video-container {
			position: relative;
			overflow: hidden;
			height: 0;
			padding-bottom: 56.25%;
			padding-top: 30px;
			iframe, object, embed {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%; } }
		.portfolio-mobile-access {
			background-color: #333;
			width: 100%;
			padding: 20px 0;
			p {
				font-size: 30px;
				color: white;
				margin-bottom: 0; } }
		.modal-content {
			display: flex;
			justify-content: center;
			height: 75%;
			width: 80%;
			.spotify-mobile {
				height: 100%; }
			iframe {
				width: 100%;
				height: 100%; } } } }
