// main: ../main.scss

@import "mixins";

/********* INDEX *********/

@include keyframes (bg-animation) {
	from {
		background-position: 0 0; }
	to {
		background-position: 200px 200px; } }

/********* AUDIO - HOMEPAGE *********/

@include keyframes (arrow) {
	0% {
		bottom: 4em; }
	20% {
		bottom: 5em; }
	40% {
		bottom: 2em; }
	100% {
		bottom: 4em; } }

@include keyframes (arrow-glow) {
	0% {
		@include filter (drop-shadow(0px 0px 0px white)); }
	20% {
		@include filter (drop-shadow(0px 0px 0px white)); }
	40% {
		@include filter (drop-shadow(0px 0px 10px white)); }
	100% {
		@include filter (drop-shadow(0px 0px 0px white)); } }

/********* GRAPHICS - HOME *********/

@include keyframes (particle1) {
	0% {
		transform: translateX(0) translateY(0) scaleX(1) scaleY(1); }
	25% {
		transform: scaleX(0.5) scaleY(0.5); }
	50% {
		transform: translateX(-100px) translateY(100px) scaleX(1) scaleY(1); }
	75% {
		transform: scaleX(0.5) scaleY(0.5); }
	100% {
		transform: translateX(0) translateY(0) scaleX(1) scaleY(1); } }

/********* GRAPHICS - PORTFOLIO *********/

@include keyframes (modalOpen) {
	0% {
		transform: translateX(450px); }
	100% {
		transform: translateX(0); } }

@include keyframes (modalClose) {
	0% {
		transform: translateX(0); }
	100% {
		transform: translateX(450px); } }

@include keyframes (modal-background-in) {
	0% {
		background-color: transparent; }
	100% {
		background-color: rgba(0, 0, 0, .8); } }

@include keyframes (modal-background-out) {
	0% {
		background-color: rgba(0, 0, 0, .8); }
	100% {
		background-color: transparent; } }
